
import XHeader from 'vux/src/components/x-header/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import AttendanceList from '@/components/attendance/AttendanceList'
export default {
name: 'StudentList',
components: {
XHeader,
Datetime,
Group,
Cell,
AttendanceList,
Flexbox,
FlexboxItem
},
data() {
return {
selectTime: '',
list: [
{value: 'xxx幼儿园', key: 0},
{value: 'xxx幼儿园', key: 1},
{value: 'xxx幼儿园', key: 2}
],
showContent: true,
titleArr: [
"姓名", "到校时间", "离校时间", "接送人", ""
],
attendanceList: [
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'},
{'name': 'name', 'age': 20, 'sex': 'F'}
]
}
},
computed: {
/**
* 获取该班级的名字
* @return {String} 获取班级名字
*/
getTitle: function() {
return this.$store.getters.getStudentAttendance.title
},
/**
* 组装该班级出勤列表
* @return {Array} 组装班级出勤列表
*/
getAttendanceList: function() {
let arr=[], list = this.$store.getters.getStudentAttendance.studentDate
for(let l in list) {
arr.push({
name: list[l].name,
arrive: list[l].arrive,
leave: list[l].leave,
parent: list[l].parent?list[l].parent:'****'
})
}
return arr
}
},
methods: {
handleDetail: function(attendance) {
this.$router.push('/teacher/attendance/monthDetail')
}
},
created()  {

this.$store.dispatch('actionStudentAttendance', {date: '2017-03-06', classId: 23})
}
}



